  export default {
    name: 'pagePermissions',
    data () {
      return {
        permissions: this.$store.getters.info.role,
        options: {
          role: this.$store.getters.info.role,
          permissions: this.$store.getters.info.permissions,
        }
      };
    },
    mounted (){
			console.log(12,this.options)
      this.$store.dispatch('setRole', this.options)
    },
    watch: {
      permissions (newQuestion, oldQuestion) {
				console.log(newQuestion, oldQuestion)
        switch (newQuestion) {
          case 'superAdmin':
            this.options = {
              role: 'superAdmin',
              permissions: '超级管理员'
            }
            break
          case 'admin':
            this.options = {
              role: 'admin',
              permissions: '管理员'
            }
            break
          case 'ordinary':
            this.options = {
              role: 'ordinary',
              permissions: '普通用户'
            }
        }
        this.$store.dispatch('setRole', this.options)
				console.log(21,this.options)

        //  刷新 tabnav 权限管理测试需要
        this.$store.dispatch('removeOtherTab', {tabItem: {
            title: 'pageControl',
            path: '/pagePermissionss'
          }, router: this.$router})
      }
    }
  }



